import { Component, OnInit } from '@angular/core';

import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
  title = 'hspminer';

  //private m_strCurrentLanuge: string = '简体中文';

  constructor(public translate: TranslateService) {
    //translate.addLangs(["zh", "en"]);
    //translate.setDefaultLang('zh');

    // const browserLang = translate.getBrowserLang();
    // translate.use(browserLang.match(/zh|en/) ? browserLang : 'zh');
  }

  public async ngOnInit() {

    this.translate.addLangs(["zh", "en"]);

    // 语言初始化(若未设置语言, 则取浏览器语言)
    let currentLanguage: string = await localStorage.getItem('currentLanguage') || this.translate.getBrowserCultureLang();  // zh-CN
    // 当在assets/i18n中找不到对应的语言翻译时，使用'zh-CN'作为默认语言
    if (currentLanguage !== 'zh' && currentLanguage !== 'en') {
      currentLanguage = 'zh';
    }
    this.translate.setDefaultLang(currentLanguage);
    //this.translate.use(currentLanguage);
    // 记录当前设置的语言
    localStorage.setItem('currentLanguage', currentLanguage);
  }
  onClickLanguage(strLanguage: string) {
    this.translate.setDefaultLang(strLanguage);
    // 记录当前设置的语言
    localStorage.setItem('currentLanguage', strLanguage);
  }

}
