import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';

import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
//import {MatButtonModule, MatCheckboxModule} from '@angular/material';
//https://material.angular.io/guide/getting-started
import { MaterialModule } from './material.module';

import { InfoComponent } from './web/info/info.component';
import { HomeComponent } from './home/home.component';
import { AboutComponent } from './about/about.component';
import { HelpComponent } from './help/help.component';
import { ProductsComponent } from './products/products.component';
import { FristpageComponent } from './home/fristpage/fristpage.component';
import { SecondpageComponent } from './home/secondpage/secondpage.component';
import { ProductpageComponent } from './home/productpage/productpage.component';
import { FooterComponent } from './footer/footer.component';
import { IcpComponent } from './icp/icp.component';

//https://www.cnblogs.com/modou/p/9527230.html    angular5 基于ngx-translate实现多语言切换
//https://github.com/ngx-translate/core
import { HttpClientModule, HttpClient } from '@angular/common/http';
import { TranslateModule, TranslateLoader } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { AeComponent } from './products/ae/ae.component';
import { GrinComponent } from './products/grin/grin.component';
import { BeamComponent } from './products/beam/beam.component';
import { BtmComponent } from './products/btm/btm.component';
import { EthComponent } from './products/eth/eth.component';
import { PerformancesComponent } from './performances/performances.component';



export function HttpLoaderFactory(httpClient: HttpClient) {
  return new TranslateHttpLoader(httpClient);
}


@NgModule({
  declarations: [
    AppComponent,
    InfoComponent,
    HomeComponent,
    AboutComponent,
    HelpComponent,
    ProductsComponent,
    FristpageComponent,
    SecondpageComponent,
    ProductpageComponent,
    FooterComponent,
    IcpComponent,
    AeComponent,
    GrinComponent,
    BeamComponent,
    BtmComponent,
    EthComponent,
    PerformancesComponent,
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    BrowserAnimationsModule,
    // MatButtonModule,
    // MatCheckboxModule
    MaterialModule,



    HttpClientModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: HttpLoaderFactory,
        deps: [HttpClient]
      }
    }),

  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
