import { Component, OnInit, ViewChild } from '@angular/core';

import { MatTabGroup } from '@angular/material';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-products',
  templateUrl: './products.component.html',
  styleUrls: ['./products.component.scss']
})
export class ProductsComponent implements OnInit {

  @ViewChild(MatTabGroup) tabGroup: MatTabGroup;
  //tabGroup: MatTabGroup;


  tabs = [
    {
      'id': "ae",
      'img': "./assets/coins/ae.png"
    },
    {
      'id': "grin",
      'img': "./assets/coins/grin.png"
    },
    {
      'id': "btm",
      'img': "./assets/coins/btm.png"
    },
    {
      'id': "eth",
      'img': "./assets/coins/eth.png"
    },
    {
      'id': "etc",
      'img': "./assets/coins/etc.png"
    },
    {
      'id': "moac",
      'img': "./assets/coins/moac.png"
    },
    {
      'id': "beam",
      'img': "./assets/coins/beam.png"
    }
  ];

  constructor(private route: ActivatedRoute) {
    //console.log(this.route.url);

  }

  ngOnInit() {
    //this.tabGroup.selectedIndex = 4;

    let strCoin: string = this.route.snapshot.params["id"];

    if (undefined === strCoin) {
      this.tabGroup.selectedIndex = 0;
      return;
    }

    this.tabGroup.selectedIndex = this.getCoinIndex(strCoin);
  }

  getCoinIndex(strCoin: string) {
    for (let i = 0; i < this.tabs.length; i++) {
      if (strCoin === this.tabs[i].id) {
        return i;
      }
    }

    return 0;
  }

}
